<template>
  <div>
    <VCalendar class="calendar" :attributes="attributes" />
  </div>
</template>

<script setup>
import { ref, computed } from "vue";

const dates = ref([
  {
    description: "レポート - テーマ選択",
    isComplete: false,
    dates: new Date(2023, 11, 1),
    color: "green",
  },
  {
    description: "レポート - 文献調査",
    isComplete: false,
    dates: new Date(2023, 11, 2),
    color: "green",
  },
  {
    description: "レポート - 下書き",
    isComplete: false,
    dates: new Date(2023, 11, 3),
    color: "green",
  },
  {
    description: "レポート - 清書",
    isComplete: false,
    dates: new Date(2023, 11, 4),
    color: "green",
  },
  {
    description: "レポート - 提出",
    isComplete: false,
    dates: new Date(2023, 11, 5),
    color: "green",
  },
  {
    description: "bbb",
    isComplete: false,
    dates: { repeat: { weekdays: 3 } }, // 毎週火曜
    color: "red",
  },
]);

const attributes = computed(() => [
  ...dates.value.map((todo) => ({
    dates: todo.dates,
    bar: {
      color: todo.color,
      ...(todo.isComplete && { class: "opacity-75" }),
    },
    popover: {
      label: todo.description,
    },
  })),
]);
</script>

<style>
.vc-bordered {
  width: 100%;
  margin-bottom: 40px;
}
</style>
